import  './AboutUs.css'
import TopBar from '../TopBar/TopBar.js'
import CheckIfLoggedOut from "../util/CheckIfLoggedOut";
import React, { Component }  from 'react';

const AboutUs = () => {
    return(
        <>
            <CheckIfLoggedOut />
            <TopBar />
            <div className="about-us">
                <h1>Equipa E-Floresta!</h1>
            </div>

            <div className="initial-text">
                O interior de Portugal est a viver um processo de desertificao populacional provocado pelo movimento
                dos mais jovens para o litoral e pelo envelhecimento da populao com graves consequncias econmicas,
                sociais e ambientais. Em particular, a gesto da floresta torna-se incomportvel, dando origem a perdas
                econmicas e ambientais resultantes dos incndios florestais. Assim, a autarquia de Mao pretende
                reordenar o seu territrio liderando uma iniciativa que promove a gesto integrada dos terrenos florestais
                para ganhar escala e reduzir custos. <p> Esta plataforma web permite registar a adeso voluntria dos proprietrios
                a esta iniciativa comunitria e, posteriormente, seguir a sua gesto em termos de custos e proveitos. </p>
            </div>
        </>
    )
}
export default AboutUs